"use client";
import { createContext, useContext, useState, ReactNode } from "react";
import axios from "axios";

const API = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5000";

type Book = {
  id: string;
  title: string;
  authors: string[];
  thumbnail?: string;
};

type BooksContextType = {
  books: Book[];
  favorites: Book[];
  fetchBooks: (query: string) => Promise<void>;
  addFavorite: (book: Book) => Promise<void>;
  removeFavorite: (id: string) => Promise<void>;
};

const BooksContext = createContext<BooksContextType | undefined>(undefined);

export const BooksProvider = ({ children }: { children: ReactNode }) => {
  const [books, setBooks] = useState<Book[]>([]);
  const [favorites, setFavorites] = useState<Book[]>([]);

  const fetchBooks = async (query: string) => {
    const res = await axios.get(`/api/books?q=${query}`);
    setBooks(res.data);
  };

  const addFavorite = async (book: Book) => {
    await axios.post(`${API}/api/favorites`, book);
    setFavorites((prev) => [...prev, book]);
  };

  const removeFavorite = async (id: string) => {
    await axios.delete(`${API}/api/favorites/${id}`);
    setFavorites((prev) => prev.filter((b) => b.id !== id));
  };

  return (
    <BooksContext.Provider
      value={{ books, favorites, fetchBooks, addFavorite, removeFavorite }}
    >
      {children}
    </BooksContext.Provider>
  );
};

export const useBooks = () => {
  const ctx = useContext(BooksContext);
  if (!ctx) throw new Error("useBooks precisa estar dentro de BooksProvider");
  return ctx;
};
